import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { HStack, PlacementWithLogical, Popover, PopoverBody, PopoverContent, PopoverTrigger, Portal, Text } from '@chakra-ui/react'
import { ReactNode } from 'react'

interface TooltipIconProps {
  tooltipCon: string | React.ReactNode
  placement?: PlacementWithLogical
  xlinkHref?: string
  children?: ReactNode
  maxW?: string
}
export default function TooltipIcon({ children, tooltipCon, placement = 'top', xlinkHref = '#icon-icon_tips', maxW = '320px' }: TooltipIconProps) {
  const tooltipObj: any = {
    setting: 'Swap Settings',
    setGasPrice: 'The total gas depends on the gas price and actual gas units consumed.',
    setMaxCapForGas: 'Set a max cap for your gas to prevent overpaying',
    createInitPrice: 'Please set an initial price for this new pool to start.',
    createPriceRange: 'Please specify a price range that you want to provide your liquidity within.',
    depositRatio: 'The ratio of two assets depends on the range you set and the current price.',
    leverage: 'This parameter indicates the concentration rate of your liquidity relative to a full range position.',
    apr24: 'Estimated according to trading activity in the past 24 hours plus mining and farming rewards',
    uniqueIdOfCoin: 'The unique id of coin',
    liquiditySources: 'Your trade is routed through one or more of these liquidity sources.',
    expiryIn: 'No extra cancellation fee will be charged for order expiry or cancellation. Gas will be consumed upon manual cancellation.',
    priceReference: 'The following price is only an external reference from 3rd party data providers, which does not reflect actual data on Cetus.',
    over: 'How many orders your want to repeat in total.',
    byEnabling:
      'By enabling this, your txn will be submitted via 3rd party MEV service provider. MEV value retained will be redistributed to all users who successfully execute their trades with Shio through its point campaign.',
    burnedLiquidity:
      ' The following liquidity has been permanently locked in the pool. Please note that LP locking could not eliminate all the risks of a token, especially when the token issuer holds a significant amount of the token.',
    vaultDetailFee: 'Commission charged from earned fees and rewards'
  }
  const { isApp } = useWindowWidth()
  return (
    <Popover isLazy trigger={isApp ? 'click' : 'hover'} placement={placement} autoFocus={false} returnFocusOnClose={false}>
      <PopoverTrigger>
        <HStack gap="0" as="button" onClick={(e: any) => cancelBubble(e)}>
          {children}
          <Icon xlinkHref={xlinkHref} />
        </HStack>
      </PopoverTrigger>
      <Portal>
        <PopoverContent w="fit-content" maxW={{ base: '300px', lg: maxW }}>
          <PopoverBody w="fit-content">
            {typeof tooltipCon === 'string' ? (
              <Text fontSize="12px" lineHeight="20px" dangerouslySetInnerHTML={{ __html: tooltipCon }} />
            ) : (
              <Text fontSize="12px" lineHeight="20px">
                {tooltipCon}
              </Text>
            )}
          </PopoverBody>
        </PopoverContent>
      </Portal>
    </Popover>
  )
}
